import LinkArrow from "./assets/LinkArrow";
import { Mail, LogoLinkedin, LogoGithub, MenuOutline } from "react-ionicons";
import About from "./components/About";
import styled, { keyframes } from "styled-components";
import Work from "./components/Work";
import { useState, useEffect, useRef } from "react";
import clsx from "clsx";
import Contact from "./components/Contact";
import { Link } from "react-scroll";
import { initializeApp } from 'firebase/app';

// TODO: Replace the following with your app's Firebase project configuration
const firebaseConfig = {
  apiKey: "AIzaSyDe1AnnQzKoIb2kmpig5FYj60h6isSFK8c",
  authDomain: "audrey-kho.firebaseapp.com",
  databaseURL: "https://audrey-kho-default-rtdb.firebaseio.com",
  projectId: "audrey-kho",
  storageBucket: "audrey-kho.appspot.com",
  messagingSenderId: "306777134790",
  appId: "1:306777134790:web:024444a1e5d426846369c7",
  measurementId: "G-1WMJFHDP7Q"
};

const app = initializeApp(firebaseConfig);

export default function App() {
  const [open, setOpen] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    const checkIfClickedOutside = (e) => {
      if (open && ref.current && !ref.current.contains(e.target)) {
        setOpen(false);
      }
    };

    document.addEventListener("mousedown", checkIfClickedOutside);

    return () => {
      document.removeEventListener("mousedown", checkIfClickedOutside);
    };
  }, [open]);

  return (
    <>
      <NavStyles>
        <h2>AUDREY KHO</h2>
        <div className="scroll-links">
          <Link to={"about"} smooth={true} offset={-132}>
            ABOUT
          </Link>
          <Link to={"work"} smooth={true} offset={-132}>
            WORK
          </Link>
          <Link to={"contact"} smooth={true} offset={-132}>
            CONTACT
          </Link>
        </div>
        <div className="main-menu">
          <a
            href="https://drive.google.com/file/d/1K4P0W8m9sU6cNN-XS0x6gC4wB7RACUpC/view"
            target="_blank"
            rel="noreferrer"
          >
            RESUME
            <LinkArrow
              width={16}
              height={16}
              stroke={2}
              color="#8787ff"
              position={-3.2}
            />
          </a>
          <a
            href="mailto:audreyckho@gmail.com"
            target="_blank"
            rel="noreferrer"
          >
            <Mail color="#d8dbdb" width="26px" height="24px" />
          </a>
          <a
            href="https://www.linkedin.com/in/audrey-kho/"
            target="_blank"
            rel="noreferrer"
          >
            <LogoLinkedin color="#d8dbdb" height="24px" />
          </a>
          <a
            href="https://github.com/audrey-kho"
            target="_blank"
            rel="noreferrer"
          >
            <LogoGithub color="#d8dbdb" width="24px" height="24px" />
          </a>
        </div>
        <button className="mobile-menu" ref={ref}>
          <MenuOutline
            color="#d8dbdb"
            width="22px"
            height="22px"
            onClick={() => {
              setOpen(!open);
            }}
          />

          <div className={clsx({ "mobile-dropdown": true, hidden: !open })}>
            <Link
              to={"about"}
              smooth={true}
              offset={-132}
              onClick={() => {
                setOpen(false);
              }}
            >
              ABOUT
            </Link>
            <Link
              to={"work"}
              smooth={true}
              offset={-132}
              onClick={() => {
                setOpen(false);
              }}
            >
              WORK
            </Link>
            <Link
              to={"contact"}
              smooth={true}
              offset={-132}
              onClick={() => {
                setOpen(false);
              }}
            >
              CONTACT
            </Link>
            <hr />
            <a
              href="https://drive.google.com/file/d/1K4P0W8m9sU6cNN-XS0x6gC4wB7RACUpC/view"
              target="_blank"
              rel="noreferrer"
              onClick={() => {
                setOpen(false);
              }}
            >
              RESUME
            </a>
            <a
              href="mailto:audreyckho@gmail.com"
              target="_blank"
              rel="noreferrer"
              onClick={() => {
                setOpen(false);
              }}
            >
              E-MAIL
            </a>
            <a
              href="https://www.linkedin.com/in/audrey-kho/"
              target="_blank"
              rel="noreferrer"
              onClick={() => {
                setOpen(false);
              }}
            >
              LINKEDIN
            </a>
            <a
              href="https://github.com/audrey-kho"
              target="_blank"
              rel="noreferrer"
              onClick={() => {
                setOpen(false);
              }}
            >
              GITHUB
            </a>
          </div>
        </button>
      </NavStyles>
      <About />
      <Work />
      <Contact firebaseConfig={app}/>
      <FooterStyles>
        <h5>&copy; AUDREY KHO 2021</h5>
      </FooterStyles>
    </>
  );
}

const dropdown = keyframes`
  from {
    transform: translateY(-6px);
    opacity: 0.4;
  }

  to {
    transform: translateY(0px);
    opacity: 1;
  }
`;

const NavStyles = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 18px;
  max-width: 1380px;
  position: sticky;
  top: 0;
  background: rgba(255, 255, 255, 0);
  backdrop-filter: blur(2.5px);
  z-index: 999;

  h2 {
    font-size: 16px;
    margin: 24px 0;
  }

  a {
    font-size: 13px;
  }

  .scroll-links {
    margin-left: 8.2vw;

    > * {
      margin: 0 18px;
    }

    @media (max-width: 850px) {
      display: none;
    }
  }

  .main-menu {
    display: flex;
    align-items: center;

    > * + * {
      margin-left: 22px;
    }

    @media (max-width: 850px) {
      display: none;
    }
  }

  .mobile-menu {
    position: relative;
    display: none;
    background: none;
    border: none;
    padding: 0;
    border-radius: 3px;
    transition: 0.3s all;
    backdrop-filter: blur(10px) !important;

    svg {
      padding: 4px 8px 2px;
    }

    .mobile-dropdown {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      font-family: "Whyte", sans-serif;
      letter-spacing: 1px;
      position: absolute;
      top: 50px;
      right: 0;
      padding: 6px 16px;
      background: rgba(21, 29, 48, 0.8);
      border: solid rgba(216, 219, 219, 0.2) 0.5px;
      border-radius: 8px;
      animation: ${dropdown} 0.2s ease;

      a {
        margin: 4px 0;
        padding: 2px;
        cursor: pointer !important;
      }

      hr {
        border: 0;
        display: block;
        width: 96%;
        background-color: #d8dbdb;
        opacity: 0.4;
        height: 0.6px;
      }

      &.hidden {
        display: none;
      }
    }

    &:hover,
    &:active,
    &:focus {
      box-shadow: inset 0 0 3px rgba(180, 180, 220, 0.6);
    }

    @media (max-width: 850px) {
      display: block;
    }
  }
`;

const FooterStyles = styled.footer`
  font-family: "Whyte Light", sans-serif;
  font-weight: 100;
  position: relative;
  padding: 24px;
  display: flex;
  font-weight: 500;
`;
